<template>
  <div id="login">
    <main>
      <section>
        <div>
          <h1>管理员登录</h1>
          <p>
            <input type="text" placeholder="管理员登录名" v-model="user" />
          </p>
          <p>
            <input type="password" placeholder="管理员登录密码" v-model="upwd" />
          </p>
          <button @click="login">登录</button>
        </div>
      </section>
    </main>
  </div>
</template>
<script>
export default {
  data() {
    return {
      user: "",
      upwd: ""
    };
  },
  methods: {
    login() {
      if (this.user != "" && this.upwd != "") {
        this.axios
          .post("index/api/admin/login", { user: this.user, upwd: this.upwd })
          .then(res => {
            if (res.data.code == 1) {
              this.$router.push("/admin/index");
              this.$message({
                message: "登陆成功",
                type: "success"
              });
            } else {
              this.$message.error("账号或密码错误");
            }
          });
      } else {
        this.$message.error("账号或密码不能为空");
      }
    }
  }
};
</script>
<style scoped>
#login {
  width: 100vw;
  height: 100vh;
  background-color: #f7f7f7;
}
main {
  width: 350px;
  height: 350px;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 20%;
  margin-left: -175px;
}
/* 字和输入框的间距 */
section > div > h1,
section > div > p {
  padding-bottom: 25px;
}
/* title的样式 */
section > div > h1 {
  font: normal 25px Helvetica, Arial, sans-serif;
  line-height: 22px;
}
section > div > h1::before {
  left: 0;
}
section > div > h1::after {
  right: 0;
}
section > div > h1:before,
section > div > h1:after {
  content: "";
  height: 1px;
  position: absolute;
  top: 10px;
  width: 20%;
  background: #7e7e7e;
}
section > div > p > input {
  width: 350px;
  height: 30px;
  padding-left: 15px;
  border-radius: 5px;
  border: 1px solid #c8c8c8;
}
/* 登录按钮样式 */
section > div > button {
  width: 60px;
  height: 30px;
}
</style>